:root {
  --primery-text-color: #434455;
  --title-text-color: --linear-gradient-bg: linear-gradient( rgba(34, 139, 34, .8), / / Темно-зелений rgba(50, 205, 50, .7) / / Яскравий зелений );
  --accent-color: #404bbf;
  --primery-background-color: #6c9a5c;
  --secondary-background-color: #4d5ae5;
  --third-text-color: #fff;
  --additional-color: #e7e9fc;
  --based-background-color: #e5e5e5;
  --team-section-color: #f4f4fd;
  --linear-gradient-bg: linear-gradient( rgba(46, 47, 66, .7), rgba(51, 66, 46, .7) );
  --border-color: #8e8f99;
  --hover-footer-color: #31d0aa;
  --time-cubic: 250ms cubic-bezier(.4, 0, .2, 1);
  --modal-window: #fcfcfc;
  --color-checkbox: #757575;
  --gold-gradient: linear-gradient(90deg, gold, #ffc107, #ffb300, gold);
  --gold-shadow: 0 0 5px rgba(255, 223, 0, .5), 0 0 10px rgba(255, 215, 0, .7), 0 0 15px rgba(255, 200, 0, .8);
}

body {
  letter-spacing: .02em;
  color: var(--primery-text-color);
  background-color: var(--primery-background-color);
  font-family: Roboto, sans-serif;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 15px;
}

h1, h2, h3, h4, p {
  margin: 0;
}

ul, ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  width: 100%;
  display: block;
}

.list {
  list-style: none;
}

.link {
  text-decoration: none;
}

input {
  padding: 0;
}

.no-scroll {
  overflow: hidden;
}

.top-header {
  width: 100%;
  background-color: var(--primery-background-color);
  border-bottom: 2px solid var(--based-background-color);
  padding-top: 24px;
  padding-bottom: 24px;
}

.top-header-container {
  max-width: 100%;
  display: flex;
}

.menu-link:hover, .menu-link:focus, .mail:hover, .number:hover, .mail:focus, .number:focus {
  color: var(--accent-color);
}

.menu, .contact {
  display: none;
}

.open-menu-btn {
  width: 32px;
  height: 32px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  margin-left: auto;
  padding: 0;
  position: relative;
}

.menu-icon-open {
  stroke: var(--title-text-color);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.mob-menu {
  z-index: 10;
  width: 100%;
  height: 100vh;
  background-color: var(--primery-background-color);
  padding-top: 40px;
  padding-bottom: 50px;
  position: fixed;
  top: 0;
}

.mob-menu > .container {
  height: 100%;
  flex-direction: column;
  justify-content: space-between;
  display: flex;
  overflow-y: auto;
}

.close-menu-btn {
  width: 24px;
  height: 24px;
  background-color: var(--additional-color);
  border: 1px solid rgba(0, 0, 0, .1);
  border-radius: 50%;
  margin-bottom: 16px;
  margin-left: auto;
  padding: 0;
  display: block;
  position: relative;
}

.menu-icon-close {
  fill: var(--title-text-color);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.close-menu-btn:-webkit-any(:hover, :focus) {
  background-color: var(--accent-color);
}

.close-menu-btn:is(:hover, :focus) {
  background-color: var(--accent-color);
}

.close-menu-btn:is(:hover, :focus) .menu-icon-close {
  fill: var(--primery-background-color);
}

.menu-mob-item:not(:last-child) {
  margin-bottom: 40px;
}

.menu-mob-link {
  letter-spacing: .02em;
  color: var(--title-text-color);
  text-transform: capitalize;
  font-size: 36px;
  font-weight: 700;
  line-height: 1.11;
}

.menu-mob-link:-webkit-any(:hover, :focus) {
  color: var(--accent-color);
}

.menu-mob-link:is(:hover, :focus) {
  color: var(--accent-color);
}

.contact-mob {
  margin-top: 40px;
  margin-bottom: 40px;
}

.mail-mob {
  letter-spacing: .02em;
  color: var(--primery-text-color);
  font-size: 20px;
  line-height: 1.2;
}

.number-mob {
  text-transform: capitalize;
  color: var(--secondary-background-color);
  font-size: 26px;
  font-weight: 600;
  line-height: 1.15385;
}

.contact-mob-number {
  margin-bottom: 40px;
}

.soc-item-mob {
  width: 40px;
  height: 40px;
  background-color: var(--secondary-background-color);
  transition: background-color var(--time-cubic);
  border-radius: 50%;
  display: inline-block;
  position: relative;
}

.soc-mob-icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.soc-item-mob:not(:last-child) {
  margin-right: 35px;
}

.soc-mob-icon {
  fill: var(--team-section-color);
}

.logo {
  letter-spacing: .03em;
  text-transform: uppercase;
  color: var(--secondary-background-color);
  margin-right: auto;
  font-family: Raleway, sans-serif;
  font-size: 18px;
  font-weight: 800;
  line-height: 1.67;
}

.logo .logo-studio-up {
  color: var(--title-text-color);
}

.logo .logo-studio-down {
  color: var(--third-text-color);
}

.modal-btn {
  min-width: 169px;
  letter-spacing: .04em;
  color: var(--third-text-color);
  background-color: var(--secondary-background-color);
  transition: backround-color var(--time-cubic), color var(--time-cubic);
  border: none;
  border-radius: 4px;
  padding: 16px 32px;
  font-size: 16px;
  line-height: 1.5;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.modal-btn:hover, .modal-btn:focus {
  background-color: var(--accent-color);
  color: var(--third-text-color);
}

.button {
  letter-spacing: .04em;
  color: var(--secondary-background-color);
  transition: color var(--time-cubic), background-color var(--time-cubic), box-shadow var(--time-cubic);
  border: 1px solid #e7e9fc;
  border-radius: 4px;
  padding: 12px 24px;
  font-weight: 500;
  text-decoration: none;
}

.button:hover, .button:focus {
  color: var(--third-text-color);
  background-color: var(--accent-color);
  cursor: pointer;
  box-shadow: 0 3px 1px rgba(0, 0, 0, .1), 0 2px 1px rgba(0, 0, 0, .08), 0 2px 2px rgba(0, 0, 0, .12);
}

.section-title {
  letter-spacing: .02em;
  color: var(--title-text-color);
  text-align: center;
  margin-bottom: 72px;
  font-size: 36px;
  line-height: 1.11;
}

.suptitle {
  color: var(--primery-text-color);
  font-size: 16px;
  line-height: 1.5;
}

.caption {
  letter-spacing: .02em;
  color: var(--title-text-color);
  margin-bottom: 8px;
  font-size: 20px;
  font-weight: 500;
  line-height: 1.2;
}

.cap {
  letter-spacing: .02em;
  text-transform: capitalize;
  color: #2e2f42;
  font-size: 36px;
  font-weight: 700;
  line-height: 1.11111;
}

.caption-port {
  letter-spacing: .02em;
  color: var(--title-text-color);
  margin-bottom: 8px;
  font-size: 20px;
  font-weight: 500;
  line-height: 1.2;
}

.suptitle-footer {
  color: var(--additional-color);
}

.hero {
  background-color: var(--title-text-color);
  background-image: var(--linear-gradient-bg), url("mob-bg.ea7443ab.jpg");
  text-align: center;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  margin: 0 auto;
  padding-top: 112px;
  padding-bottom: 112px;
}

@media (min-device-pixel-ratio: 2), (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2x) {
  .hero {
    background-image: var(--linear-gradient-bg), url("mob-bg-2x.f2428394.jpg");
  }
}

.hero-container {
  padding: 0;
}

.hero-title {
  width: 318px;
  color: var(--third-text-color);
  max-width: 494px;
  text-transform: uppercase;
  color: #fcfc30;
  margin-bottom: 72px;
  margin-left: auto;
  margin-right: auto;
  font-size: 24px;
  line-height: 1.11;
}

.hero-p {
  width: 300px;
  color: var(--third-text-color);
  margin-left: auto;
  margin-right: auto;
  font-size: 22px;
  font-weight: 500;
  line-height: 1.11;
}

.golden-effect {
  background: var(--gold-gradient);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-background-clip: text;
  text-shadow: var(--gold-shadow);
  font-size: 2em;
  font-weight: bold;
  animation: shimmer 3s infinite;
}

@keyframes shimmer {
  0% {
    background-position: 0%;
  }

  100% {
    background-position: 100%;
  }
}

.golden-border {
  border-image: var(--gold-gradient);
  border: 5px solid rgba(0, 0, 0, 0);
  animation: border-shine 5s linear infinite;
}

@keyframes border-shine {
  from {
    border-image-source: linear-gradient(45deg, gold, #ffc107, #ffb300, gold);
  }

  to {
    border-image-source: linear-gradient(45deg, #ffc107, #ffb300, gold, #ffc107);
  }
}

.button-none {
  display: none;
}

section {
  outline: 2px solid tomato;
}

.section-buttton {
  margin: 0 auto;
  padding-top: 48px;
  padding-bottom: 48px;
}

.portfolio {
  margin-left: auto;
  margin-right: auto;
  padding: 0 15px;
}

.filters-button, .filters-button-second {
  flex-direction: row;
  display: flex;
}

.filters-button {
  margin-bottom: 16px;
}

.filters-button-project:not(:last-child) {
  margin-right: 24px;
}

.content {
  flex-wrap: wrap;
  gap: 48px;
  margin-top: 48px;
  display: flex;
}

.content-title {
  width: 100%;
  transition: box-shadow var(--time-cubic);
}

.content-title:is(:hover, :focus) .overlay {
  transform: translateY(0);
}

.anim-window {
  position: relative;
  overflow: hidden;
}

.overlay {
  height: 100%;
  color: var(--team-section-color);
  background-color: var(--secondary-background-color);
  transition: transform var(--time-cubic);
  padding: 40px 32px 0;
  font-size: 16px;
  line-height: 1.5;
  position: absolute;
  top: 0;
  overflow: auto;
  transform: translateY(100%);
}

.portfolio-card {
  border: 1px solid var(--additional-color);
  background-color: #fff;
  padding-top: 32px;
  padding-bottom: 32px;
  padding-left: 16px;
}

.content-title:-webkit-any(:hover, :focus) {
  box-shadow: 0 1px 6px rgba(46, 47, 66, .08);
}

.content-title:is(:hover, :focus) {
  box-shadow: 0 1px 6px rgba(46, 47, 66, .08);
}

.icon-features {
  display: none;
}

.our {
  opacity: 0;
  display: none;
}

.section-features {
  margin: 0 auto;
  padding-top: 96px;
  padding-bottom: 96px;
}

.features-list {
  flex-wrap: wrap;
  gap: 72px;
  display: flex;
}

.features-list-title {
  width: 100%;
}

.feature-list .title {
  color: var(--title-text-color);
}

.section-work {
  display: none;
}

.team-list {
  flex-wrap: wrap;
  justify-content: center;
  gap: 72px;
  display: flex;
}

.team-list-title {
  width: 264px;
}

.team-card {
  background-color: var(--third-text-color);
  text-align: center;
  border-radius: 0 0 4px 4px;
  padding: 32px 16px;
  box-shadow: 0 1px 6px rgba(46, 47, 66, .08), 0 1px 1px rgba(46, 47, 66, .16), 0 2px 1px rgba(46, 47, 66, .08);
}

.team-soc-list {
  justify-content: center;
  gap: 24px;
  display: flex;
}

.team-soc-item {
  width: 40px;
  height: 40px;
}

.team-soc-link {
  width: 100%;
  height: 100%;
  background-color: var(--secondary-background-color);
  transition: background-color var(--time-cubic);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.team-soc-icon {
  fill: var(--team-section-color);
}

.team-soc-link:-webkit-any(:hover, :focus) {
  background-color: var(--accent-color);
}

.team-soc-link:is(:hover, :focus) {
  background-color: var(--accent-color);
}

.section-customers {
  margin: 0 auto;
  padding-top: 96px;
  padding-bottom: 96px;
}

.list-customers {
  flex-wrap: wrap;
  justify-content: center;
  gap: 72px 16px;
  display: flex;
}

.item-customers {
  height: 88px;
  flex-basis: calc(50% - 8px);
}

.link-customers {
  width: 100%;
  height: 100%;
  border: 1px solid var(--border-color);
  transition: border-color var(--time-cubic);
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.customers-icon {
  fill: var(--border-color);
  transition: fill var(--time-cubic);
}

.link-customers:-webkit-any(:hover, :focus) {
  border-color: var(--accent-color);
}

.link-customers:is(:hover, :focus) {
  border-color: var(--accent-color);
}

.link-customers:is(:hover, :focus) .customers-icon {
  fill: var(--accent-color);
}

.footer {
  background-color: var(--title-text-color);
  margin: 0 auto;
  padding-top: 96px;
  padding-bottom: 96px;
}

.container-footer {
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

.flex-table {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  display: flex;
}

.footer-text {
  width: 268px;
  margin-bottom: 72px;
}

.suptitle-footer {
  max-width: 264px;
  margin: 0 auto;
}

.social-media {
  width: 208px;
}

.social {
  letter-spacing: .02em;
  color: var(--third-text-color);
  text-align: center;
  font-family: Roboto;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.5;
}

.social-icons {
  justify-content: center;
  gap: 16px;
  margin-top: 16px;
  display: flex;
}

.social-items {
  width: 40px;
  height: 40px;
}

.social-link {
  width: 100%;
  height: 100%;
  transition: background-color var(--time-cubic);
  background-color: rgba(255, 255, 255, .1);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.icons-footer {
  fill: var(--team-section-color);
}

.social-link:-webkit-any(:hover, :focus) {
  background-color: var(--hover-footer-color);
}

.social-link:is(:hover, :focus) {
  background-color: var(--hover-footer-color);
}

.input {
  width: 100%;
}

.footer-form {
  margin-top: 72px;
}

.form .social {
  margin-bottom: 16px;
}

.form-input {
  min-width: 290px;
  height: 40px;
  color: var(--third-text-color);
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid rgba(255, 255, 255, .3);
  border-radius: 4px;
  outline: rgba(0, 0, 0, 0);
  padding: 8px 16px;
}

.form-field {
  width: 100%;
}

.form-field input::-ms-input-placeholder {
  color: rgba(255, 255, 255, .6);
  padding-right: 8px 16px;
  font-size: 12px;
  font-weight: 400;
  line-height: 2;
}

.form-field input::placeholder {
  color: rgba(255, 255, 255, .6);
  padding-right: 8px 16px;
  font-size: 12px;
  font-weight: 400;
  line-height: 2;
}

.btn-form {
  width: 165px;
  letter-spacing: .04em;
  color: var(--third-text-color);
  background-color: var(--secondary-background-color);
  border: 0;
  border-radius: 4px;
  margin-top: 16px;
  margin-left: auto;
  margin-right: auto;
  padding: 8px 64px 8px 24px;
  font-size: 16px;
  line-height: 1.5;
  display: block;
  position: relative;
}

.icon {
  fill: var(--primery-background-color);
  transform: translateY(-50%, 0);
  position: absolute;
  top: 8px;
  right: 24px;
}

.backdrop {
  width: 100%;
  height: 100%;
  z-index: 100;
  background-color: rgba(46, 47, 66, .4);
  transition: opacity .55s linear, visibility .55s linear;
  position: fixed;
  top: 0;
  left: 0;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.modal {
  transform: translate(-50%, -50%) rotate(0);
  min-width: 408px;
  min-height: 576px;
  background-color: var(--modal-window);
  border-radius: 4px;
  padding-left: 24px;
  padding-right: 24px;
  transition: transform .55s cubic-bezier(.48, -.01, 1, .01);
  position: absolute;
  top: 50%;
  left: 50%;
  box-shadow: 0 1px 1px rgba(0, 0, 0, .14), 0 1px 3px rgba(0, 0, 0, .12), 0 2px 1px rgba(0, 0, 0, .2);
}

.backdrop.is-hidden .modal {
  transform: translate(70%, -75%)rotate(145deg);
}

.btn-modal-close {
  width: 24px;
  height: 24px;
  background-color: var(--additional-color);
  border: 1px solid rgba(0, 0, 0, .1);
  border-radius: 50%;
  margin-top: 24px;
  margin-left: auto;
  padding: 0;
  display: block;
  position: relative;
}

.icon-close {
  fill: var(--title-text-color);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.btn-modal-close:-webkit-any(:hover, :focus) {
  background-color: var(--accent-color);
}

.btn-modal-close:is(:hover, :focus) {
  background-color: var(--accent-color);
}

.btn-modal-close:is(:hover, :focus) .icon-close {
  fill: var(--primery-background-color);
}

.modal-title {
  letter-spacing: .02em;
  color: var(--title-text-color);
  text-align: center;
  margin-top: 72px;
  margin-bottom: 16px;
  font-size: 16px;
  line-height: 1.5;
}

.modal-form {
  outline: rgba(0, 0, 0, 0);
  margin-bottom: 24px;
}

.modal-item {
  width: 100%;
  border: 1px solid rgba(33, 33, 33, .2);
  border-radius: 4px;
  outline: rgba(0, 0, 0, 0);
}

.modal-input {
  height: 40px;
  transition: border var(--time-cubic);
  margin: 0;
  padding-top: 11px;
  padding-bottom: 11px;
  padding-left: 38px;
}

.modal-input:focus, .textarea-modal:focus {
  border: 1px solid var(--secondary-background-color);
}

.modal-input:focus + .modal-icon-input {
  fill: var(--secondary-background-color);
}

.modal-field {
  margin-bottom: 8px;
}

.privacy-form {
  outline: rgba(0, 0, 0, 0);
}

.check-hidden {
  width: 0;
  height: 0;
  margin: 0;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.modal-label {
  letter-spacing: .01em;
  color: #8e8f99;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.7;
}

.modal-svg {
  position: relative;
}

.modal-icon-input {
  transition: fill var(--time-cubic);
  position: absolute;
  top: 50%;
  left: 16px;
  transform: translateY(-50%);
}

.textarea-form {
  margin-bottom: 16px;
}

.textarea-modal {
  width: 100%;
  height: 120px;
  resize: none;
  transition: border var(--time-cubic);
  border: 1px solid rgba(33, 33, 33, .2);
  border-radius: 4px;
  outline: rgba(0, 0, 0, 0);
  padding: 8px 16px;
}

.textarea-modal::-ms-input-placeholder {
  letter-spacing: .04em;
  color: rgba(117, 117, 117, .5);
  padding-right: 16px;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.7;
}

.textarea-modal::placeholder {
  letter-spacing: .04em;
  color: rgba(117, 117, 117, .5);
  padding-right: 16px;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.7;
}

.checkbox-label {
  letter-spacing: .04em;
  color: var(--color-checkbox);
  margin-top: 0;
  font-size: 12px;
  line-height: 1.3;
  display: flex;
}

.check-policy {
  color: var(--secondary-background-color);
  margin-left: 2px;
}

.checkbox-label span {
  width: 16px;
  height: 16px;
  fill: rgba(0, 0, 0, 0);
  border: 1.25px solid #2e2f42;
  border-radius: 2px;
  justify-content: center;
  align-items: center;
  margin-right: 8px;
  display: flex;
}

.privacy-form:checked + .checkbox-label span {
  background-color: var(--accent-color);
  fill: var(--team-section-color);
  border: none;
}

.check-btn {
  justify-content: center;
  margin-top: 24px;
  display: flex;
}

@media screen and (max-width: 767px) {
  .caption, .social {
    text-align: center;
  }

  .footer-logo {
    text-align: center;
    display: block;
  }
}

.section-team {
  background-color: var(--team-section-color);
  margin: 0 auto;
  padding-top: 96px;
  padding-bottom: 128px;
